<template>
  <div class="detail">
    <div class="goBack"
         @click="goBack">
      <img src="data:image/png;base64,"
           alt="">
    </div>
    <div v-if="filmInfo">
      <img :src="filmInfo.poster"
           class="poster">
      <div class="film_conent">
        <div class="col">
          <div class="film_name">
            <span class="name">{{ filmInfo.name }}</span>
            <span class="item">{{ filmInfo.item.name }}</span>
          </div>
          <div class="grade">{{ filmInfo. grade }}{{ filmInfo.grade ? '' : '&nbsp;'}}</div>
        </div>
        <div class="category text">{{ filmInfo.category }}</div>
        <div class="time text">{{filmInfo.premiereAt | dateFilter }}</div>
        <div class="runtime text">{{ filmInfo.nation }} | {{ filmInfo.runtime }} </div>
        <div class="hidde text">{{ filmInfo.synopsis }}</div>
      </div>
      <div class="film_actors">
        <div class="actors_title"></div>
        <actor-swiper class="actor-swiper"
                      :actors='filmInfo.actors'></actor-swiper>
      </div>
      <div class="photos-title">
        <div class="photos-title-text"></div>
        <photo-swiper class="photo-swiper"
                      :photo='filmInfo.photos'></photo-swiper>
      </div>
      <div class="toBuyTicket"></div>
    </div>
  </div>
</template>
<script>
import { getDetail } from '@/api/film/detail'
import ActorSwiper from './Detail/ActorSwiper'
import PhotoSwiper from './Detail/PhotoSwiper'
import { Indicator } from 'mint-ui'
// import eventBus from '@/eventbus'
export default {
  name: 'Detail',
  components: {
    ActorSwiper,
    PhotoSwiper
  },
  props: ['id'],
  data () {
    return {
      filmInfo: null
    }
  },
  activated () {
    Indicator.open()
    this.$store.commit('city/hideTabbar', false)
    getDetail(this.id).then(res => {
      console.log(res.data)
      this.filmInfo = res.data.data.film
      Indicator.close()
    })
  },
  deactivated () {
    //   eventBus.$emit('maizuo', true)
    this.$store.commit('city/showTabbar', true)
    console.log('')
  },
  methods: {
    goBack () {
      this.$router.go(-1)
    }
  }

}
</script>
<style lang="scss" scoped>
div {
  box-sizing: border-box;
  display: block;
}
.poster {
  width: 100%;
}
.goBack {
  height: 30px;
  position: absolute;
  top: 5px;
  left: 5px;
  img {
    width: 30px;
  }
}
.film_conent {
  padding: 15px;
  padding-top: 12px;
  background-color: #fff;
  .col {
    display: flex;
    -webkit-box-pack: start;
    justify-content: flex-start;
    -webkit-box-align: center;
    align-items: center;
    .film_name {
      width: 256px;
      span {
        vertical-align: middle;
      }
      .name {
        color: #191a1b;
        font-size: 18px;
        height: 24px;
        line-height: 24px;
        margin-right: 7px;
      }
      .item {
        font-size: 9px;
        color: #fff;
        background-color: #d2d6dc;
        height: 14px;
        line-height: 14px;
        padding: 0 2px;
        border-radius: 2px;
        display: inline-block;
      }
    }
    .grade {
      width: calc(100% - 250px);
      text-align: right;
      color: #ffb232;
      font-size: 18px;
    }
  }
  .text {
    font-size: 13px;
    color: #797d82;
    margin-top: 7px;
  }
  .hidde {
    font: 13px/1.5 Tahoma, Helvetica, Arial, sans-serif;
    height: 38px !important;
    overflow: hidden;
    margin-top: 12px;
  }
}
.film_actors {
  margin-top: 10px;
  background-color: #fff;
  .actors_title {
    padding: 15px;
    width: 100%;
  }
  .actor-swiper {
    padding-left: 15px;
  }
}
.photos-title {
  margin-top: 10px;
  height: 62px;
  .photos-title-text {
    padding: 15px;
    height: 62px;
    width: 100%;
  }
  .photo-swiper {
    padding-left: 15px;
  }
}
.toBuyTicket {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 49px;
  text-align: center;
  background-color: #ff5f16;
  color: #fff;
  font-size: 16px;
  line-height: 49px;
  z-index: 1000;
}
</style>
